import * as React from "react";
import { PropsWithChildren } from "react";
import ReactHtmlParse from "react-html-parser";

import Card, { IPropsCard } from "../../../Card/Card";

export type PropsSector = PropsWithChildren<{
  title: string | null | undefined;
  resume_html: string | null | undefined;
  entities: IPropsCard[] | null | undefined;
}>;

const Sector: React.FC<PropsSector> = (props: PropsSector) => {
  return (
    <div className={`m-sector`}>
      <h3 className={"sector__title"}>{props.title} </h3>
      <div className="c-sector">
        {props.resume_html && <div className={"sector__resume"}>{ReactHtmlParse(props.resume_html)}</div>}
        {props.entities && (
          <div className="sector__cards">
            {props.entities.map((entity, index) => (
              <Card title={entity.title} tagTitle={"h4"} resume_html={entity.resume_html} key={index} />
            ))}
          </div>
        )}
      </div>
    </div>
  );
};

export default Sector;
