import * as React from "react";
import { useEffect, useRef } from "react";
import ReactHtmlParse from "react-html-parser";

import { IData } from "../../../types/IData";
import DoNotMountComponent from "../../DoNotMountComponent/DoNotMountComponent";
import Sector, { PropsSector } from "./Sector/Sector";

// export type PropsSector = {
//   title: string;
//   resume_html: string;
//   entities: IPropsCard[];
// };

type PropsSectionSegments = {
  sectionSegments: IData;
  sectorList: PropsSector[];
  nameSection: string | null | undefined;
};
const SectionSegments: React.FC<PropsSectionSegments> = ({
  sectionSegments,
  sectorList,
  nameSection,
}: PropsSectionSegments) => {
  if (!sectionSegments.title) return <DoNotMountComponent sms={"no hay titulo"} />;
  if (!sectorList) return <DoNotMountComponent sms={"no hay sectores"} />;
  const sectors: PropsSector[] = sectorList;
  const headline = useRef() as React.MutableRefObject<HTMLDivElement>;
  const heading = useRef() as React.MutableRefObject<HTMLDivElement>;
  const title_render = React.createElement(
    sectionSegments?.tagTitle || "p",
    { className: "a-title-section", ref: heading },
    sectionSegments.title
  );
  function setHeightDashLine() {
    const { height: headlineHeight } = headline.current.getBoundingClientRect();
    const { height: headingHeight } = heading.current.getBoundingClientRect();
    const autoHeight = headlineHeight - headingHeight;
    const heightDashLine = autoHeight < 30 ? 30 : autoHeight;
    headline.current.style.setProperty("--height-dash-line", `${heightDashLine}px`);
    headline.current.style.setProperty("--shift-dash-line", `${headingHeight}px`);
  }

  useEffect(() => {
    setHeightDashLine();
    window.addEventListener("resize", setHeightDashLine);
    return () => window.removeEventListener("resize", setHeightDashLine);
  }, []);

  return (
    <section className={`s-${nameSection} o-section-segments`}>
      <div className={`c-section-segments`}>
        <div ref={headline} className="section-segments__headline">
          {title_render}
          {sectionSegments.resume_html && (
            <div className={"section-segments__mark"}>{ReactHtmlParse(sectionSegments.resume_html)}</div>
          )}
        </div>
        {sectors.map((sector, index: number) => (
          <Sector key={index} title={sector.title} resume_html={sector.resume_html} entities={sector.entities} />
        ))}
      </div>
    </section>
  );
};

export default SectionSegments;
