import { graphql, PageProps } from "gatsby";
import React from "react";

import { Language } from "../../nubarchiva/settings/translations";
import Agreements from "../modules/content/Agreements/Agreements";
import BannerPhotoHome from "../modules/content/BannerPhoto/BannerPhotoHome";
import FAQs from "../modules/content/FAQs/FAQs";
import { Faq } from "../modules/content/FAQs/IFaqs";
import ResumeAndDemo from "../modules/content/ResumeAndDemo/ResumeAndDemo";
import SectionSegments from "../modules/content/SectionSegments/SectionSegments";
import { PropsSector } from "../modules/content/SectionSegments/Sector/Sector";
import TitleResumeAndForm, { RenderForm } from "../modules/content/TitleResumeAndForm/TitleResumeAndForm";
import { CtaDemo, Hero } from "../modules/Hero/Hero";
import Layout from "../modules/Layout/Layout";
import { SEO } from "../modules/SEO/SEO";
import TitleMarkAndResume from "../modules/TitleMarkAndResume/TitleMarkAndResume";
import UsesNubarchiva from "../modules/UseNubarchiva/UsesNubarchiva";
import {IPropsCard} from "../modules/Card/Card";
import {ILink} from "../modules/Layout/header/navbarLinks/NavbarLinks";
import {IPageProps} from "../types/IData";

const IndexPage = ({ data, pageContext }: IPageProps<Queries.HomePageQuery>) => {
  const publicSector: PropsSector = {
    title: data.page?.content?.SectionSegments?.public_sector?.title,
    resume_html: data.page?.content?.SectionSegments?.public_sector?.resume_html,
    entities: data.sectorsPublic.nodes.map((node) => ({
      title: node.summary?.title,
      resume_html: node.summary?.description,
    })),
  };
  const privateSector: PropsSector = {
    title: data.page?.content?.SectionSegments?.private_sector?.title,
    resume_html: data.page?.content?.SectionSegments?.private_sector?.resume_html,
    entities: data.sectorsPrivate.nodes.map((node) => ({
      title: node.summary?.title,
      resume_html: node.summary?.description,
    })),
  };

  const usesList = data.allUse.nodes.map((use) => ({
    slug: use.metas.slug,
    title: use.summary?.title,
    description: use.summary?.description,
  }));
  return (
    <Layout
      lang={data.page?.lang as Language}
      landingName="home"
      useLinks={data.allUse.nodes.map((item) => item.metas)}
      legalLinks={data.allLegals?.nodes?.map((item) => item.childMarkdownRemark?.frontmatter) as ILink[] || []}
      navbarLinksList={[...data.navbarLinks.nodes.map((item) => item.metas)]}
    >
      <Hero
        subtitle={data.page?.content?.hero?.subtitle}
        ctaDemo={CtaDemo.GO_TO_DEMO}
        modifier={data.page?.settings?.id}
        backgroundImage={data.backgroundHero?.sharp?.gatsbyImageData}
      >
        <p className={"hero__title"}>{data.page?.content?.hero?.title}</p>
      </Hero>
      <TitleMarkAndResume
        titleMarkAndResume={{ ...data.page?.content?.main, tagTitle: "h1" }}
        cardList={{ list: data.page?.content?.goals as IPropsCard[], tagTitles: "h3" }}
        nameSection={"software-os"}
      />
      <ResumeAndDemo image={data.resumeAndDemoPhoto?.sharp?.gatsbyImageData} data={{ ...data.page?.content?.resumeAndDemo, tagTitle: "h2" }} nameSection={"any-examples"} />
      <UsesNubarchiva UsesList={usesList} />
      <Agreements agreements={{ tagTitle: "h2" }} />
      <BannerPhotoHome />
      <SectionSegments
        sectorList={[publicSector, privateSector]}
        sectionSegments={{ ...data.page?.content?.SectionSegments }}
        nameSection={"section-segments"}
      />
      <FAQs
        title={data.page?.content?.faqs?.title}
        faqsLists={data.page?.content?.faqs?.FaqsList as Faq[]}
        tagTitle={"h2"}
      />
      <TitleResumeAndForm
        data={{ ...data.page?.content?.form, tagTitle: "p" }}
        nameSection={"subscribe-home"}
        renderForm={RenderForm.SUBSCRIBE}
      />
    </Layout>
  );
};

export default IndexPage;

export const Head = ({ data }: IPageProps<Queries.HomePageQuery>) => (
  <SEO
    title={data.page?.metas.title}
    description={data.page?.metas.description}
    thumbnailWithExtension={"home.png"}
    canonical={"/"}
    slug={"/"}
    lang={data.page?.lang}
    hrefLangList={data.hrefLangsList?.nodes?.map((node) => ({
      hrefLang: node.lang,
      slug: node.metas.slug,
    }))}
  />
);

export const query = graphql`
  query HomePage($id: String!, $settingsId: String!, $lang: String = "es_es") {
    page(id: { eq: $id }) {
      ...seo
      lang
      settings {
        id
      }
      content {
        hero {
          title
          subtitle
        }
        main {
          title
          resume_html
          mark_html
        }
        goals {
          title
          resume_html
        }
        resumeAndDemo {
          title
          resume_html
          button
        }
        SectionSegments {
          title
          resume_html
          public_sector {
            title
            resume_html
          }
          private_sector {
            title
            resume_html
          }
        }
        faqs {
          title
          FaqsList {
            answer
            question
          }
        }
        form {
          title
        }
      }
    }
    backgroundHero: file(name: { eq: "home" }, ext: { eq: ".jpg" }) {
      sharp: childImageSharp {
        gatsbyImageData
      }
    }
    resumeAndDemoPhoto: file(name: { eq: $settingsId }, relativeDirectory: {eq: "resume-and-demo"}) {
      sharp: childImageSharp {
        gatsbyImageData
      }
      ext
    }
    allUse(filter: { lang: { eq: $lang } }, sort: { settings: { order: ASC } }) {
      nodes {
        metas {
          slug
          name
        }
        summary {
          title
          description
        }
      }
    }
    sectorsPublic: allSector(
      filter: { lang: { eq: $lang }, target: { eq: PUBLIC } }
      sort: { settings: { order: ASC } }
    ) {
      nodes {
        target
        summary {
          title
          description
        }
      }
    }
    sectorsPrivate: allSector(
      filter: { lang: { eq: $lang }, target: { eq: PRIVATE } }
      sort: { settings: { order: ASC } }
    ) {
      nodes {
        target
        summary {
          title
          description
        }
      }
    }
    navbarLinks: allPage(filter: { lang: { eq: $lang } }, sort: { settings: { order: ASC } }) {
      nodes {
        metas {
          name
          slug
        }
      }
    }

    hrefLangsList: allPage(filter: { settings: { id: { eq: $settingsId } } }) {
      nodes {
        metas {
          slug
        }
        lang
      }
    }
    allLegals: allFile(
      filter: { sourceInstanceName: { eq: "legal" }, relativeDirectory: { eq: $lang } }
      sort: { childMarkdownRemark: { frontmatter: { order: ASC } } }
    ) {
      nodes {
        childMarkdownRemark {
          frontmatter {
            slug
            name: title
          }
          id
        }
        relativeDirectory
      }
    }
  }
`;
