import * as React from "react";
import { PropsWithChildren, useEffect, useRef } from "react";
import { useTranslation } from "react-i18next";

import DoNotMountComponent from "../DoNotMountComponent/DoNotMountComponent";
import Navigation from "../Navigator/Navigation";
import useSlider from "../Slider/useSlider";
import UseItem from "./UseItem/UseItem";

export type Use = {
  slug: string | null | undefined;
  title: string | null | undefined;
  description: string | null | undefined;
};

type PropsUsesNubarchiva = PropsWithChildren<{
  // avoidSlugSector?: string;
  UsesList?: Use[];
  tagTitle?: "h1" | "h2" | "h3" | "p" | "h4" | "h5" | "h6" | undefined;
}>;

const UsesNubarchiva: React.FC<PropsUsesNubarchiva> = ({ UsesList, tagTitle }: PropsUsesNubarchiva) => {
  const sliderUses = useRef() as React.MutableRefObject<HTMLDivElement>;
  const oUsesNubarchiva = useRef() as React.MutableRefObject<HTMLDivElement>;
  const { t } = useTranslation("global");
  const { prev, isCanPrev, isCanNext, next } = useSlider(sliderUses);
  if (!t("uses.title")) return <DoNotMountComponent sms={"No tiene titulo"} />;
  if (!UsesList || UsesList.length === 0) {
    return <DoNotMountComponent sms={"No hay item "} />;
  }
  const title_render = React.createElement(tagTitle || "p", { className: "a-title-section" }, t("uses.title"));

  useEffect(() => {
    if (isCanNext || isCanPrev) {
      oUsesNubarchiva.current.style.setProperty("--align-content", "flex-start");
      oUsesNubarchiva.current.style.setProperty("--display-navigation", "flex");
    } else {
      oUsesNubarchiva.current.style.setProperty("--align-content", "center");
      oUsesNubarchiva.current.style.setProperty("--display-navigation", "none");
    }
  }, [isCanNext, isCanPrev]);

  return (
    <section ref={oUsesNubarchiva} className={`o-uses-nubarchiva`}>
      <div className={`c-uses-nubarchiva`}>
        <div className={"a-title-section"}>{title_render}</div>
      </div>
      <div ref={sliderUses} className={`cs-uses-nubarchiva`}>
        {UsesList.map((sector: Use) => (
          <UseItem sector={sector} key={sector.slug} />
        ))}
      </div>
      <Navigation controls={{ back: prev, next, isCanNext, isCanBack: isCanPrev }} />
    </section>
  );
};

export default UsesNubarchiva;
