import * as React from "react";
import { useEffect, useState } from "react";

const useSlider = (ref: React.MutableRefObject<HTMLDivElement>) => {
  const [move, setMove] = useState(350);
  const [isCanNext, setIsCanNext] = useState(true);
  const [isCanPrev, setIsCanPrev] = useState(false);
  const [index, setIndex] = useState<number>(0);

  function onClickCard(position: number) {
    if (position === index) {
      return;
    }
    if (position > index) {
      next();
    }
    if (position < index) {
      prev();
    }
    setIndex(position);
  }

  function endOfStroke() {
    if (!ref.current) {
      return;
    }
    if (ref.current.scrollLeft < 20) {
      setIsCanPrev(false);
    } else {
      setIsCanPrev(true);
    }
    if (ref.current.scrollWidth - ref.current.scrollLeft <= ref.current.clientWidth + 20) {
      setIsCanNext(false);
    } else {
      setIsCanNext(true);
    }
  }

  function calculateMove() {
    if (ref.current && ref.current.children.length > 0) {
      const element = ref.current.children[0] as HTMLDivElement;
      setMove((ref.current.scrollWidth - element.offsetWidth) / ref.current.children.length);
    }
  }

  function next() {
    goto(+1);
  }

  function prev() {
    goto(-1);
  }

  useEffect(() => {
    if (ref.current && ref.current.children.length > 0) {
      calculateMove();
      endOfStroke();
      ref.current.addEventListener("scroll", endOfStroke);
      // window.addEventListener("keydown", keyboard);
      window.addEventListener("resize", calculateMove);
    }
    return () => {
      if (ref.current) {
        ref.current.removeEventListener("scroll", endOfStroke);
      }
      window.removeEventListener("resize", calculateMove);
      // window.removeEventListener("keydown", keyboard);
    };
  }, []);
  const goto = (index: number) => {
    if (!ref.current) {
      return;
    }
    ref.current.scrollLeft = ref.current.scrollLeft + move * index;
  };

  return { next, prev, isCanNext, isCanPrev, onClickCard };
};

export default useSlider;
