import * as React from "react";
import { PropsWithChildren } from "react";

import { next_svg, preview_svg } from "./elements_navigation";

type PropsNavigator = PropsWithChildren<{
  controls: {
    back: () => void;
    next: () => void;
    isCanBack: boolean;
    isCanNext: boolean;
  };
}>;

const Navigation: React.FC<PropsNavigator> = (props: PropsNavigator) => {
  const { back, isCanNext, isCanBack, next } = props.controls;
  return (
    <div className={"m-navigation"}>
      <button
        tabIndex={0}
        onClick={back}
        aria-label={"desplazar a la izquierda"}
        className={`navigation__button ${isCanBack ? "" : "navigation__button--disable"}`}
      >
        {preview_svg}
      </button>
      <button
        tabIndex={0}
        onClick={next}
        aria-label={"desplazar a la derecha"}
        className={`navigation__button ${isCanNext ? "" : "navigation__button--disable"}`}
      >
        {next_svg}
      </button>
    </div>
  );
};

export default Navigation;
